<template>
    <v-select
            v-model="val"
            taggable
            label="Name"
            :options="teachers"
            :create-option="createTeacher"
            placeholder="Преподаватель"
    />
</template>

<script>
    import {createNamespacedHelpers} from 'vuex';

    const {mapActions} = createNamespacedHelpers('teachers');

    export default {
        name: 'Teachers',
        props: ['value'],
        data: () => ({
            teachers: [],
        }),
        computed: {
            val: {
                get() {
                    return this.value;
                },
                set(value) {
                    this.$emit('input', value);
                },
            },
        },
        mounted() {
            this.load();
        },
        methods: {
            ...mapActions(['get', 'create']),
            async load() {
                this.teachers = await this.get();
            },
            async createTeacher(label) {
                const option = {
                    Name: label,
                };

                const teacher = await this.create(option);
                option.Id = teacher.Id;
                this.$emit('option:created', option);

                return option;
            },
        },
    }
</script>
