<template>
    <v-select
            v-model="val"
            label="Name"
            :options="housings"
            :reduce="housing => housing.Id"
            placeholder="Корпус"
    />
</template>

<script>
    import {getHousings} from '@/util/api';

    export default {
        name: 'Housings',
        props: ['value'],
        data: () => ({
            housings: [],
        }),
        computed: {
            val: {
                get() {
                    return this.value;
                },
                set(value) {
                    this.$emit('input', value);
                },
            },
        },
        mounted() {
            this.load();
        },
        methods: {
            async load() {
                this.housings = await getHousings();
            },
        },
    }
</script>
