<template>
    <v-select
            v-model="val"
            label="Name"
            :options="groups"
            :reduce="group => group.Id"
            placeholder="Группа"
    />
</template>

<script>
    import {getGroups} from '@/util/api';

    export default {
        name: 'Groups',
        props: ['value', 'housing'],
        data: () => ({
            groups: [],
        }),
        computed: {
            val: {
                get() {
                    return this.value;
                },
                set(value) {
                    this.$emit('input', value);
                },
            },
        },
        mounted() {
            this.load();
        },
        methods: {
            async load() {
                this.groups = await getGroups(null, this.housing);
            },
        },
    }
</script>
