<template>
    <v-select
            v-model="val"
            taggable
            label="Name"
            :options="audiences"
            :create-option="createAudience"
            placeholder="Аудитория"
    />
</template>

<script>
    import {createNamespacedHelpers} from 'vuex';

    const {mapActions} = createNamespacedHelpers('audiences');

    export default {
        name: 'Audience',
        props: ['value', 'housing'],
        data: () => ({
            audiences: [],
        }),
        computed: {
            val: {
                get() {
                    return this.value;
                },
                set(value) {
                    this.$emit('input', value);
                },
            },
        },
        mounted() {
            this.load();
        },
        methods: {
            ...mapActions(['get', 'create']),
            async load() {
                this.audiences = await this.get();
            },
            async createAudience(label) {
                const option = {
                    Name: label,
                    HousingId: this.housing.Id,
                };

                const audience = await this.create(option);
                option.Id = audience.Id;
                this.$emit('option:created', option);

                return option;
            },
        },
    }
</script>
